<template>
  <div>
    <detail-banner :sightName="sightName" :bannerImgs="gallaryImgs" :bannerImg="bannerImg"></detail-banner>
    <detail-header></detail-header>
    <div class="content">
      <detail-list :list="list"></detail-list>
    </div>
  </div>
</template>

<script>
import DetailBanner from './components/Banner'
import DetailHeader from './components/Header'
import DetailList from './components/List'
import axios from 'axios'
export default {
  name: 'Detail',
  components: {
    DetailBanner,
    DetailHeader,
    DetailList
  },
  data () {
    return {
      list: [{
        "title": "",
        "children": [{
          "title": "",
          "children": [{
            "title": " - "
          }]
        },{
          "title": ""
        }]
      }, {
        "title": ""
      }, {
        "title": ""
      }, {
        "title": ""
      }],
      sightName: '(AAAA)',
      bannerImg: 'http://img1.qunarzz.com/sight/p0/201404/23/04b92c99462687fa1ba45c1b5ba4ad77.jpg_600x330_bf9c4904.jpg',
      gallaryImgs: ["http://img1.qunarzz.com/sight/p0/201404/23/04b92c99462687fa1ba45c1b5ba4ad77.jpg_800x800_70debc93.jpg", "http://img1.qunarzz.com/sight/p0/1709/76/7691528bc7d7ad3ca3.img.png_800x800_9ef05ee7.png"],
    }
  },
  methods:{
    getDetailData() {
      axios.get('/api/detail.json',{
        params:{
          id: this.$route.params.id
        }
      }).then(this.getDataSucc)
    },
    getDataSucc(res) {
      console.log(res);
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.list = data.categoryList
        this.gallaryImgs = data.gallaryImgs
        this.sightName = data.sightName
        this.bannerImg = data.bannerImg
      }
    }
  },
  mounted() {
    // this.getDetailData()
  },
}
</script>

<style lang="stylus" scoped>
.content
  height 50rem
</style>



